@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap");
* {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
button,
dd {
  margin: 0;
  padding: 0;
}

body {
  line-height: 1.4;
  font-size: 14px;
  font-weight: 500;
  font-family: "Inter", sans-serif;
  color: #292f36;
}

::selection {
  background-color: #d34178;
  color: #ffffff;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

ul {
  list-style: none;
}

.logo {
  overflow: hidden;
  position: relative;
  display: inline-block;
  text-decoration: none;
}
.logo::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.7), transparent);
  transform: skewX(-25deg);
  animation: lightRay 5s infinite;
}
@keyframes lightRay {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}

.box-content {
  border-radius: 16px;
  background-color: #fffffb;
  box-shadow: 0px 0px 32px 0px rgba(162, 173, 185, 0.2);
  padding: clamp(0.938rem, -1.071rem + 5.357vw, 1.5rem);
}
@media only screen and (max-width: 424px) {
  .box-content {
    border-radius: unset;
    background-color: unset;
    box-shadow: unset;
    padding: 5px;
  }
}

.read-more {
  color: #ff6b6c;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.4285714286;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  transition: color 0.2s ease-in;
  flex-shrink: 0;
}
.read-more:hover {
  color: #c64e4f;
}

.title {
  color: #292f36;
  font-size: clamp(1.125rem, 0.97rem + 0.583vw, 1.25rem);
  font-weight: 600;
  line-height: 1.4;
  letter-spacing: -0.2px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 80;
  display: none;
}

.close-btn {
  position: absolute;
  top: 1rem;
  right: 0.8rem;
  cursor: pointer;
  z-index: 85;
  color: #ffffff;
  font-size: 18px;
  text-transform: uppercase;
}
@media only screen and (max-width: 374px) {
  .close-btn {
    display: none;
  }
}

.no-scroll {
  overflow-y: hidden;
}

.wrapper {
  display: grid;
  grid-template-columns: 240px 1fr;
  height: 100vh;
}
@media only screen and (max-width: 991px) {
  .wrapper {
    display: unset;
  }
}

.gallery-image {
  border-radius: 8px;
  height: 0;
  position: relative;
  overflow: hidden;
}
.gallery-image > * {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.gallery-image--full {
  padding-bottom: 34.25%;
}
.gallery-image--half {
  padding-bottom: 48.25%;
}

.homepage-title {
  color: #292f36;
  font-size: clamp(1.75rem, 1.398rem + 1.127vw, 2.75rem);
  font-weight: 600;
  line-height: clamp(2.188rem, -1.75rem + 8.203vw, 3.5rem);
}

.homepage-readmore {
  color: #be232b;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  margin-top: clamp(0.938rem, -3.75rem + 9.766vw, 2.5rem);
  text-decoration: none;
  padding: 12px 24px;
  position: relative;
  transform: translateX(-50%);
  left: 50%;
  transition: all 0.3s ease;
  border-radius: 100rem;
}
.homepage-readmore:hover {
  color: #ffffff;
  background-color: #ff6b6c;
}
.homepage-readmore:hover img {
  filter: brightness(0) invert(1);
}

.homepage-desc {
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
}

.homeHeader {
  padding: 12px 0;
}
.homeHeader-left ul {
  display: flex;
  gap: 20px;
  align-items: center;
  margin-bottom: 20px;
}
.homeHeader-left ul li a {
  text-decoration: none;
  color: #292f36;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.3333333333;
  letter-spacing: -0.24px;
  transition: color 0.2s ease;
}
.homeHeader-left ul li a :hover {
  color: #ff6b6c;
}
.homeHeader-right {
  display: flex;
  gap: 24px;
}
.homeHeader-right__btn {
  color: #ff6b6c;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.4285714286;
  padding: 8px 24px;
  border-radius: 8px;
  transition: opacity 0.2s ease;
}
.homeHeader-right__btn:hover {
  opacity: 0.8;
}
.homeHeader-right__btn--channel {
  background-color: #FFF6F3;
  color: #ff6b6c;
}
.homeHeader-right__btn--login {
  background-color: #FF6B6C;
  color: #ffffff;
}

.search {
  margin-top: clamp(0.625rem, -6.648rem + 11.364vw, 1.875rem);
}
.search-main {
  max-width: 800px;
  height: 45px;
  margin: 0 auto;
  border-radius: 6px;
  border: 1.5px solid #a2adb9;
  background-color: #fffffb;
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
}
.search-main input {
  padding: 12px 16px;
  width: 100%;
  height: 100%;
  font-family: inherit;
  color: #474554;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  outline: none;
  border: none;
}
.search-main button {
  background-color: #ff6b6c;
  padding: 10px 12px;
  transition: opacity 0.2s ease;
}
.search-main button:hover {
  opacity: 0.8;
}
.search-keyword {
  max-width: 600px;
  margin: 0 auto;
  margin-top: 15px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 12px;
}
.search-keyword li a {
  text-decoration: none;
  color: #aca9bb;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: -0.24px;
  padding: 8px 12px;
  border-radius: 8px;
  border: 1px solid #aca9bb;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  transition: all 0.2s ease;
}
.search-keyword li a:hover {
  background-color: #ff6b6c;
  color: #ffffff;
  border-color: #ff6b6c;
}
.search-keyword li a:hover img {
  filter: brightness(0) invert(1);
}

.banner {
  margin-top: 25px;
}
.banner-item {
  height: 0;
  position: relative;
  padding-bottom: 30.95%;
}
.banner-item > * {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.banner .owl-carousel .owl-nav .owl-prev,
.banner .owl-carousel .owl-nav .owl-next {
  color: #ff6b6c;
  font-size: 28px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: clamp(2.5rem, 1.88rem + 2.332vw, 3rem);
  height: clamp(2.5rem, 1.88rem + 2.332vw, 3rem);
  border-radius: 100rem;
  border: 1px solid #fffffb;
  background: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(20px);
  transition: all 0.2s ease;
}
.banner .owl-carousel .owl-nav .owl-prev {
  left: 10px;
}
.banner .owl-carousel .owl-nav .owl-next {
  right: 10px;
}
.banner .owl-carousel .owl-nav .owl-prev:hover,
.banner .owl-carousel .owl-nav .owl-next:hover {
  color: #ffffff;
  background-color: #ff6b6c;
}
.banner .owl-carousel .owl-nav [class*=owl-] {
  display: block;
}
@media only screen and (max-width: 767px) {
  .banner-item {
    padding-bottom: 50%;
  }
}

.category {
  margin-top: clamp(2.5rem, 1.18rem + 4.225vw, 6.25rem);
}
.category-list {
  margin-top: clamp(1.25rem, 0.282rem + 3.099vw, 2.5rem);
}
.category-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.category-item__image {
  width: clamp(4.688rem, 3.681rem + 3.79vw, 5.5rem);
  height: clamp(4.688rem, 3.681rem + 3.79vw, 5.5rem);
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 100rem;
  border: 1px solid #a2adb9;
}
.category-item__content {
  margin-top: 8px;
  text-decoration: none;
  color: #606f80;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  gap: 2px;
  transition: color 0.2s ease;
}
.category-item__content h4 {
  text-align: center;
  font-size: clamp(0.875rem, 0.72rem + 0.583vw, 1rem);
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.16px;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-word;
}
.category-item__content span {
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: -0.24px;
}

.business {
  padding-top: clamp(1.25rem, -2.321rem + 5.58vw, 4.375rem);
}
.business-list {
  padding: clamp(0.625rem, -1.518rem + 3.348vw, 2.5rem);
  text-align: center;
}
.business-list h4 {
  color: #292f36;
  font-size: clamp(1.5rem, 0.75rem + 1.563vw, 1.75rem);
  font-weight: 600;
  line-height: 36px;
  letter-spacing: -0.56px;
}
.business-list ul {
  margin-top: clamp(0.25rem, -2rem + 4.688vw, 1rem);
  display: flex;
  flex-direction: column;
  gap: clamp(0.5rem, -1.75rem + 4.688vw, 1.25rem);
  padding-left: 24px;
}
.business-list ul li a {
  text-decoration: none;
  color: #606f80;
  font-size: clamp(1.031rem, 0.375rem + 1.367vw, 1.25rem);
  font-weight: 600;
  line-height: 28px;
  letter-spacing: -0.2px;
  transition: color 0.2s ease;
}
.business-list ul li a:hover {
  color: #ff6b6c;
}
.business-image {
  height: 0;
  position: relative;
  padding-bottom: 53.35%;
}
.business-image img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media only screen and (min-width: 768px) {
  .business .business-list {
    text-align: left;
  }
  .business .business-list ul {
    list-style: disc;
  }
}

.homepageProduct {
  margin: clamp(1.875rem, 0.951rem + 2.958vw, 4.5rem) 0;
}
.homepageProduct-list {
  margin-top: clamp(1.25rem, 0.282rem + 3.099vw, 2.5rem);
}
.homepageProduct-item {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.homepageProduct-item__image {
  border-radius: 12px;
  overflow: hidden;
  border: 1px solid #fff8eb;
  height: 0;
  position: relative;
  padding-bottom: 125.55%;
}
.homepageProduct-item__image:hover img {
  transform: scale(1.2);
  opacity: 0.8;
}
.homepageProduct-item__image img {
  transition: all 0.3s ease;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.homepageProduct-item__image span {
  position: absolute;
  top: 0;
  left: 0;
  padding: 8px 16px;
  border-radius: 12px;
  margin: 10px;
  background: rgba(255, 255, 251, 0.32);
  backdrop-filter: blur(20px);
  color: #ff6b6c;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
}
.homepageProduct-item__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 124px;
}
.homepageProduct-item__content h4 a {
  text-decoration: none;
  color: #292f36;
  font-size: 20px;
  font-weight: 600;
  line-height: 28px;
  letter-spacing: -0.2px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-word;
  transition: color 0.2s ease;
}
.homepageProduct-item__content h4 a:hover {
  color: #ff6b6c;
}
.homepageProduct-item__content strong {
  color: #ff6b6c;
  font-size: 20px;
  font-weight: 600;
  line-height: 28px;
  letter-spacing: -0.2px;
  margin: 4px 0 6px 0;
  display: block;
}
.homepageProduct-item__content-price {
  display: flex;
  align-items: center;
  border-radius: 4px;
  border: 1.5px solid #ff6b6c;
  width: fit-content;
}
.homepageProduct-item__content-price img {
  max-width: 16px;
}
.homepageProduct-item__content-price span {
  padding: 2px 5px;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}
.homepageProduct .owl-carousel .owl-nav .owl-prev,
.homepageProduct .owl-carousel .owl-nav .owl-next {
  color: #ff6b6c;
  font-size: 28px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: clamp(2.5rem, 1.88rem + 2.332vw, 3rem);
  height: clamp(2.5rem, 1.88rem + 2.332vw, 3rem);
  border-radius: 100rem;
  border: 1px solid #fffffb;
  background: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(20px);
  transition: all 0.2s ease;
}
.homepageProduct .owl-carousel .owl-nav .owl-prev {
  left: 10px;
}
.homepageProduct .owl-carousel .owl-nav .owl-next {
  right: 10px;
}
.homepageProduct .owl-carousel .owl-nav .owl-prev:hover,
.homepageProduct .owl-carousel .owl-nav .owl-next:hover {
  color: #ffffff;
  background-color: #ff6b6c;
}
.homepageProduct .owl-carousel .owl-nav [class*=owl-] {
  display: block;
}
@media only screen and (max-width: 559px) {
  .homepageProduct .homepageProduct-item {
    gap: 10px;
  }
  .homepageProduct .homepageProduct-item__content h4 a {
    font-size: 18px;
    line-height: 22px;
  }
  .homepageProduct .homepageProduct-item__content-price img {
    display: none;
  }
  .homepageProduct .homepageProduct-item__content-price span {
    padding: 4px 8px;
    font-size: 12px;
  }
}

.sell {
  margin-top: clamp(5rem, -3.17rem + 12.766vw, 8rem);
}
.sell-tab {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 10px 25px;
  margin: 24px auto clamp(2.188rem, -5.472rem + 11.968vw, 5rem) auto;
  width: fit-content;
}
.sell-tab span {
  color: #606f80;
  font-size: clamp(1.25rem, 0.569rem + 1.064vw, 1.5rem);
  font-weight: 600;
  line-height: 32px;
  user-select: none;
  cursor: pointer;
}
.sell-tab span.active {
  color: #ff6b6c;
  pointer-events: none;
}
.sell-pane {
  display: none;
}
.sell-pane.active {
  display: block;
}
.sell-item {
  border-radius: 24px;
  box-shadow: 0px 4px 24px 0px rgba(162, 173, 185, 0.16);
  overflow: hidden;
  height: 0;
  position: relative;
  padding-bottom: 144%;
}
@media (hover: hover) {
  .sell-item:hover .sell-item__content {
    background-color: #ffc145;
  }
  .sell-item:hover .sell-item__image {
    opacity: 0.8;
    transform: scale(1.1);
  }
}
.sell-item__image, .sell-item__content {
  position: absolute;
  left: 0;
  width: 100%;
}
.sell-item__image {
  top: 0;
  height: 100%;
  object-fit: cover;
  transition: all 0.2s ease;
}
.sell-item__content {
  padding: 0 clamp(0.938rem, -3.269rem + 4.808vw, 2.5rem);
  min-height: clamp(5rem, 1.205rem + 14.286vw, 6.563rem);
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 0;
  text-align: center;
  background-color: #ff6b6c;
  transition: background-color 0.2s ease;
}
.sell-item__content h2 a {
  color: #fffffb;
  font-size: clamp(1.031rem, 0.898rem + 0.502vw, 1.5rem);
  font-weight: 600;
  line-height: clamp(1.438rem, 0.071rem + 5.143vw, 2rem);
  text-decoration: none;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-word;
}
@media only screen and (max-width: 575px) {
  .sell-item {
    border-radius: unset;
  }
  .sell-pane-list {
    display: grid;
    justify-content: flex-start;
    grid-auto-columns: 80%;
    grid-auto-flow: column;
    grid-gap: 1.2rem;
    overflow: auto;
    overflow-y: hidden;
    scroll-snap-type: x mandatory;
    scroll-snap-stop: always;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    -webkit-overflow-scrolling: touch;
    scroll-padding: 1rem;
    grid-template-columns: unset;
  }
  .sell-pane-list::-webkit-scrollbar {
    display: none;
    width: 0;
  }
  .sell-pane-list > * {
    scroll-snap-align: start;
  }
}

.faq {
  margin: clamp(5rem, -4rem + 18.75vw, 8rem) 0;
}
.faq-accordion {
  margin-top: -20px;
}
.faq-accordion-item:not(:last-child) {
  border-bottom: 1px solid #a2adb9;
}
.faq-accordion-icon {
  border-radius: 100rem;
  color: #ffffff;
  background-color: #ff6b6c;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 40px;
  height: 40px;
}
.faq-accordion-header {
  color: #292f36;
  font-size: clamp(1.125rem, 0.97rem + 0.583vw, 1.25rem);
  font-style: normal;
  font-weight: 600;
  line-height: 28px;
  letter-spacing: -0.2px;
  cursor: pointer;
  width: 100%;
  border: none;
  outline: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: left;
  gap: 20px;
  padding: 20px 0;
  transition: color 0.2s ease;
}
.faq-accordion-header:hover {
  color: #ff6b6c;
}
.faq-accordion-content {
  padding: 0 60px 40px 0;
