  font-size: clamp(0.875rem, 0.72rem + 0.583vw, 1rem);
  font-weight: 400;
  line-height: 24px;
}

.experience-main {
  padding: clamp(3.125rem, -7.362rem + 16.387vw, 8rem) 36px;
  border-radius: clamp(1rem, -2.556rem + 7.407vw, 3rem);
  background-color: #ff6b6c;
  color: #ffffff;
}
.experience-btn {
  color: #fffffb;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  border-radius: 8px;
  padding: 12px 24px;
  transition: opacity 0.2s ease;
  border: 1px solid transparent;
  outline: none;
}
.experience-btn:hover {
  opacity: 0.8;
}
.experience-btn--first {
  background: #292f36;
}
.experience-btn--second {
  background: #ff6b6c;
  border-color: #ffffff;
}

#back-to-top {
  --size-button: 50px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: block;
  z-index: 80;
  border: none;
  outline: none;
  cursor: pointer;
  width: var(--size-button);
  height: var(--size-button);
  font-size: 20px;
  border-radius: 5px;
  opacity: 0;
  transition: opacity 0.3s ease, transform 0.3s ease;
  transform: translateY(calc(100% + 40px));
}
#back-to-top.show {
  opacity: 1;
  transform: translateY(0);
}
@media (hover: hover) {
  #back-to-top:hover {
    opacity: 0.8;
  }
}
@media (max-width: 599px) {
  #back-to-top {
    --size-button: 45px;
  }
}

.footer {
  padding: clamp(4.25rem, 2.831rem + 5.342vw, 6.25rem) 0;
}
.footer-list {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  gap: clamp(1.875rem, -10.489rem + 19.318vw, 4rem);
}
@media only screen and (max-width: 1023px) {
  .footer-list {
    grid-template-columns: 1fr 1fr;
  }
}
@media only screen and (max-width: 767px) {
  .footer-list {
    grid-template-columns: 1fr;
  }
}
.footer-desc {
  color: #606f80;
  font-size: 14px;
  font-weight: 400;
  line-height: 25px;
  margin-top: clamp(0.5rem, -2.688rem + 6.641vw, 1.563rem);
}
.footer-title {
  font-size: 18px;
